<template>
  <div class="thermostat">
    <div class="left-icon">
      <img v-show="homeOccupied" src="../assets/ohouse370.png" height="185" width="185">
      <img v-show="!homeOccupied" src="../assets/house370.png" height="185" width="185">
    </div>
    <div class="temp-section">
      <div class="number-input">
        <NumberInput title="Min" :value="desiredMinTemperature" @valueChanged="handleMinChange"/>
        <NumberInput title="Max" :value="desiredMaxTemperature" @valueChanged="handleMaxChange"/>
      </div>
      <div>
        Actual: {{actualTemperature}}
      </div>
    </div>
    <div class="right-icon">
        <img v-show="isFurnaceRunning" src="../assets/flame370.png" height="185" width="185">
        <img v-show="isAcRunning" src="../assets/snow370.png" height="185" width="185">
    </div>
  </div>

</template>

<script>
import NumberInput from './NumberInput.vue';

export default {
  name: 'Thermostat',
  data() {
    return {
      unused: false,
    };
  },
  created() {
  },
  components: {
    NumberInput,
  },
  methods: {
    handleMinChange(delta) {
      const min = this.desiredMinTemperature + delta;
      let max = this.desiredMaxTemperature;
      if (this.inRange(min)) {
        if (min > max) {
          max = min;
        }
        this.$store.dispatch('setDesiredRange', { min, max });
      }
    },
    handleMaxChange(delta) {
      const max = this.desiredMaxTemperature + delta;
      let min = this.desiredMinTemperature;
      if (this.inRange(max)) {
        if (min > max) {
          min = max;
        }
        this.$store.dispatch('setDesiredRange', { min, max });
      }
    },
    inRange(value) {
      return (value >= 10) && (value <= 35);
    },
  },
  computed: {
    isFurnaceRunning() {
      return this.$store.state.thermostat.furnaceRunning;
    },
    isAcRunning() {
      return this.$store.state.thermostat.acRunning;
    },
    desiredMinTemperature() {
      return this.$store.state.thermostat.desiredRange.min;
    },
    desiredMaxTemperature() {
      return this.$store.state.thermostat.desiredRange.max;
    },
    actualTemperature() {
      return this.$store.state.thermostat.actualTemperature;
    },
    homeOccupied() {
      return this.$store.state.thermostat.homeOccupied;
    },
  },
};
</script>


