<template>
  <div class="sim">
      Set Actual: <input v-model="actualTemperature" class="slider" type="range"><br><br>
      Home occupied: <input v-model="homeOccupied" type="checkbox">
  </div>

</template>

<script>

export default {
  name: 'Simulator',
  data() {
    return {
      unused: false,
    };
  },
  created() {
    const homeOccupied = this.$store.state.sim.homeOccupied;
    const actualTemperature = this.$store.state.sim.actualTemperature;
    this.$store.dispatch('setSimulator', { homeOccupied, actualTemperature });
  },
  computed: {
    actualTemperature: {
      get() {
        return this.$store.state.sim.actualTemperature;
      },
      set(actualTemperature) {
        const homeOccupied = this.$store.state.sim.homeOccupied;
        this.$store.dispatch('setSimulator', { homeOccupied, actualTemperature });
      },
    },
    homeOccupied: {
      get() {
        return this.$store.state.sim.homeOccupied;
      },
      set(homeOccupied) {
        const actualTemperature = this.$store.state.sim.actualTemperature;
        this.$store.dispatch('setSimulator', { homeOccupied, actualTemperature });
      },
    },
  },
};
</script>


<style scoped>
.sim {
  background-color: white;
  padding: 20px;
}
.slider {
    width: 80%;
}
</style>
