import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    thermostat: {
      acRunning: false,
      furnaceRunning: true,
      desiredMinTemperature: 20,
      desiredMaxTemperature: 40,
      homeOccupied: true,
      actualTemperature: 35,
    },
    sim: {
      homeOccupied: true,
      actualTemperature: 25,
    },
  },
  mutations: {
    updateThermostatState(state, thermostat) {
      this.state.thermostat = thermostat;
    },
    updateSimulatorState(state, simState) {
      this.state.sim.actualTemperature = simState.actualTemperature;
      this.state.sim.homeOccupied = simState.homeOccupied;
    },
  },
  actions: {
    getThermostatState({ commit }) {
      axios.get('/api/thermostat-state')
        .then(result => commit('updateThermostatState', result.data))
        .catch(console.error); // eslint-disable-line no-console
    },
    setDesiredRange({ commit }, range) {
      axios.put('/api/desired-range', range)
        .then(result => commit('updateThermostatState', result.data))
        .catch(error => console.log(error)); // eslint-disable-line no-console
    },
    setSimulator({ commit }, simState) {
      axios.put('/api/sim/simulator-state', simState)
        .then(result => commit('updateThermostatState', result.data))
        .then(() => commit('updateSimulatorState', simState))
        .catch(error => console.log(error)); // eslint-disable-line no-console
    },
  },
});
